  padding: 10px;
}

.nav-langauge .nav-dropdown {
  position: absolute;
  right: 0;
  top: 40px;
  display: none;
  margin: 0;
  padding: 10px 0;
  list-style: none;
  width: 120px;
  background: #fff;
  border-radius: 9px;
  box-shadow: 0 3px 12px 0 rgba(0, 0, 0, 0.15);
}
