  display: flex;
  flex-direction: column;
  align-items: center;
}
.nav-langauge:hover .nav-dropdown li {
  margin: 0;
  display: block;
  padding: 5px 0;
}
.nav-langauge .nav-dropdown li div,
.nav-langauge #country {
  background-repeat: no-repeat;
}

