.accordion {
  width: 100%;
}
.panel {
  color: #2bc5b4;
  margin-bottom: 15px;
}
.panel__label {
  /* position: relative;
    display: block;
    width: 100%;
    background: none; */
  border: none;
  /* text-align: left;
    padding: 25px 60px 25px 25px;
    font-weight: 500;
    font-size: 17px;
    font-family: inherit;
    transition: color 0.2s linear;
    cursor: pointer; */

  width: 100%;
  padding: 15px 70px;
  cursor: pointer;
  transform: translate3d(0, 0, 0);
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.73;
  letter-spacing: 0.44px;
  text-align: left;
  color: #1c1d3e;
  position: relative;
  font-size: 16px;
  /* background: #1569a8; */
  margin-bottom: -1px;
  /* border-bottom: 1px solid #0e4671; */
  border-radius: 7px;
  box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.12);
  background-color: #ffffff;
  text-align: left;
}
.panel__label:focus {
  outline: none;
}
.panel__label:after,
.panel__label:before {
  content: "";
  position: absolute;
  left: 20px;
  top: 50%;
  width: 22px;
  height: 2px;
  margin-top: -2px;
  background-color: #372717;
}
.panel__label:before {
  transform: rotate(-90deg);
  transition: transform 0.35s cubic-bezier(0.65, 0.05, 0.36, 1);
}
.panel[aria-expanded="true"] .panel__content {
  opacity: 1;
}
.panel[aria-expanded="true"] .panel__label {
  color: #fff;
  transition: background 0.5s;
  background: #2bc5b4;
  color: #fff;
}
.panel[aria-expanded="true"] .panel__label:before {
  transform: rotate(0deg);
  color: #fff;
}
.panel[aria-expanded="true"] .panel__label:after {
  background-color: #fff;
}
.panel__inner {
  overflow: hidden;
  will-change: height;
  transition: height 0.4s cubic-bezier(0.65, 0.05, 0.36, 1);
}
.panel__content {
  margin: 25px 25px;
  font-size: 14px;
  color: #756658;
  opacity: 0;
  transition: opacity 0.3s linear 0.18s;
}

