  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-template-rows: auto;
  grid-gap: 0 1rem;
  gap: 0 1rem;
}

.partner-form > ul > li:nth-of-type(3) {
  grid-column: 1/-1;
}

.partner-form > ul > li:nth-of-type(4) {
  grid-column: 1/-1;
}

.partner-form > ul > li {
  position: relative;
  display: flex;
  margin: 0 0 3rem 0;
